// src/components/BudgetList/BudgetList.tsx
"use client";

import { useEffect } from "react";
import { useBudget } from "../../context/BudgetContext"; // useBudget hook'u
import { toast } from "react-toastify"; // react-toastify'i içeri aktar
import 'react-toastify/dist/ReactToastify.css'; // Toastify stil dosyasını içeri aktar
import styles from './BudgetList.module.css';
import { ToastContainer } from "react-toastify"; 

export default function BudgetList() {
  const { transactions, setTransactions } = useBudget(); // Verileri context'ten alıyoruz

  useEffect(() => {
    // Burada sadece verileri Context API'ye alıyoruz, LocalStorage ile ilgili işlem yapılmaz
  }, [setTransactions]);

  const handleDelete = (id: string) => {
    // Silme işlemi: Kullanıcıdan onay alıyoruz
    const isConfirmed = window.confirm("Bu işlemi silmek istediğinizden emin misiniz?");
    if (isConfirmed) {
      // Kullanıcı onay verirse işlemi siliyoruz
      setTransactions((prevTransactions) =>
        prevTransactions.filter((transaction) => transaction.id !== id)
      );
      // Silindi mesajını toast olarak gösteriyoruz
      toast.error("Kategori başarıyla silindi!"); // Hata mesajı (silindi)
    }
  };

  return (
    <div className={styles["list-container"]}>
      <h1>Bütçe Listesi</h1>
      {/* Bütçe limiti gösterimi */}

      {transactions.length === 0 ? (
        <p className={styles["empty-message"]}>Henüz eklenmiş bir işlem yok.</p>
      ) : (
        <table className={styles["table"]}>
          <thead>
            <tr>
              <th>Tip</th>
              <th>Kategori</th>
              <th>Açıklama</th>
              <th>Tutar</th>
              <th>Tarih</th>
              <th>Sil</th> {/* Silme butonu için yeni bir başlık */}
            </tr>
          </thead>
          <tbody>
            {transactions.map((transaction) => (
              <tr key={transaction.id}>
                <td>{transaction.type === "income" ? "Gelir" : "Gider"}</td>
                <td>{transaction.category}</td>
                <td>{transaction.description}</td>
                <td>{transaction.amount.toFixed(2)} TL</td>
                <td>{transaction.date}</td>
                <td>
                  <button onClick={() => handleDelete(transaction.id)} className={styles["delete-button"]}>Sil</button>
                </td> {/* Silme butonu */}
              </tr>
            ))}
          </tbody>
        </table>
      )}
      <ToastContainer position="top-right" autoClose={3000} />
    </div>
  );
}
